// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import 'theme/style.css'

window.commentSection = 'aaa'

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App></App>',
  components: { App }
})

window.publishHeight = function () {
  var currentHeight = getBodyHeight()

  parent.postMessage("height=" + currentHeight, "*")
}


window.getBodyHeight = function () {

  var height,
    scrollHeight,
    offsetHeight

  if (document.height) {
    height = document.height
  } else if (document.body) {
    if (document.body.scrollHeight) {
      height = scrollHeight = document.body.scrollHeight
    }

    if (document.body.offsetHeight) {
      height = offsetHeight = document.body.offsetHeight
    }

    if (scrollHeight && offsetHeight) {
      height = Math.max(scrollHeight, offsetHeight)
    }
  }

  return height
}

window.getViewPortHeight = function () {
  var height = 0

  if (window.innerHeight) {
    height = window.innerHeight - 18
  } else if ((document.documentElement) && (document.documentElement.clientHeight)) {
    height = document.documentElement.clientHeight
  } else if ((document.body) && (document.body.clientHeight)) {
    height = document.body.clientHeight
  }

  return height
}

window.parseQueryString = function (url) {
  url = new String(url)

  var queryStringValues = new Object()
  var querystring = url.substring((url.indexOf('?') + 1), url.length)
  var querystringSplit = querystring.split('&')

  for (var i = 0; i < querystringSplit.length; i++) {
    var pair = querystringSplit[i].split('=')
    var name = pair[0]
    var value = pair[1]

    queryStringValues[name] = value
  }

  return queryStringValues
}

window.onload = function (event) {
  window.setInterval(window.publishHeight, 300)
}



