<template>
  <div>
    <Heading>Comments</Heading>
    <div v-for="comment in comments">
      <Comment
        :userName="comment.userInfo.name"
        :mail="comment.userInfo.mail"
        :date="comment.date"
        :text="comment.name"
        :avatarUrl="comment.userInfo.avatar"/>
    </div>
  </div>
</template>

<script>
import Comment from 'theme/Comment'
import Heading from 'theme/Heading'

export default {

  props: ['comments'],
  components: {
    Comment,
    Heading
  }
}
</script>



