<template>
  <span>
    <h3>Before commenting, you must login</h3>
    <PostCommentLoginGoogle :onLogin="onLoginDone" style="float: left; width: 60px"/>
    <PostCommentLoginFacebook :onLogin="onLoginDone" style="float: left; width: 60px"/>
    <div style="clear: both;"></div>
    <PostCommentLoginMail :onLogin="onLoginDone"/>
  </span>
</template>

<script>
import PostCommentLoginMail from './PostCommentLoginMail'
import PostCommentLoginGoogle from './PostCommentLoginGoogle'
import PostCommentLoginFacebook from './PostCommentLoginFacebook'

export default {
  props: ['loginInfo', 'onLogin'],

  methods: {
    onLoginDone: function (loginInfo) {
      if (this.onLogin) {
        this.onLogin(loginInfo)
      }
    }
  },

  components: {
    PostCommentLoginMail,
    PostCommentLoginGoogle,
    PostCommentLoginFacebook
  },

  data: function () {
    return {}
  }
}
</script>
<style>
  h3 {
    text-align: left;
  }
</style>



