<template>
  <div id="app" style="minHeight: 500px">
    <PostCommentForm :comments="comments" :arrayRef="$firebaseRefs.comments" :commentStreams="$firebaseRefs.commentstreams" />
    <div style="clear: both;"></div>
    <CommentsList :comments="comments"/>
  </div>
</template>

<script>
import PostCommentForm from './components/PostCommentForm'
import CommentsList from './components/CommentsList'
import VueFire from 'vuefire'
import firebase from 'firebase'
import Vue from 'vue'
import VueTimeago from 'vue-timeago'
import '../temporary_gapi'
import GSignInButton from 'vue-google-signin-button'
import FBSignInButton from 'vue-facebook-signin-button'

Vue.use(FBSignInButton)

window.fbAsyncInit = function () {
  window.FB.init({
    appId: '386369495149670',
    cookie: true,  // enable cookies to allow the server to access the session
    xfbml: true,  // parse social plugins on this page
    version: 'v2.8' // use graph api version 2.8
  })
};

(function (d, s, id) {
  var js
  var fjs = d.getElementsByTagName(s)[0]
  if (d.getElementById(id)) return
  js = d.createElement(s)
  js.id = id
  js.src = '//connect.facebook.net/en_US/sdk.js'
  fjs.parentNode.insertBefore(js, fjs)
}(document, 'script', 'facebook-jssdk'))

Vue.use(GSignInButton)

Vue.use(VueTimeago, {
  name: 'timeago', // component name, `timeago` by default
  locale: 'en-US',
  locales: {
    // you will need json-loader in webpack 1
    'en-US': require('vue-timeago/locales/en-US.json')
  }
})

// explicit installation required in module environments
Vue.use(VueFire)
Vue.config.productionTip = false

var firebaseApp = firebase.initializeApp({
  apiKey: 'AIzaSyAXmXfo3bReHMnNCAHVJgZjFPXgFyta6PE',
  authDomain: 'gwenaelp-comments-system.firebaseapp.com',
  databaseURL: 'https://gwenaelp-comments-system.firebaseio.com',
  projectId: 'gwenaelp-comments-system',
  storageBucket: 'gwenaelp-comments-system.appspot.com',
  messagingSenderId: '467955741111'
})

var parseQueryString = function (url) {
  url = new String(url)

  var queryStringValues = new Object()
  var querystring = url.substring((url.indexOf('?') + 1), url.length)
  var querystringSplit = querystring.split('&')

  for (var i = 0; i < querystringSplit.length; i++) {
    var pair = querystringSplit[i].split('=')
    var name = pair[0]
    var value = pair[1]

    queryStringValues[name] = value
  }

  return queryStringValues
}

var db = firebaseApp.database()

var qs = parseQueryString(window.location.href)

window.uid = qs.uid
window.commentStream = qs.channel

export default {
  name: 'app',

  data: {
    message: 'Hello Vue!'
  },

  firebase: {
    // simple syntax, bind as an array by default
    comments: db.ref('/' + window.uid + '/' + window.commentStream + '/comments'),
    commentstreams: db.ref('/' + window.uid + '/commentstreams')
  },

  components: {
    PostCommentForm,
    CommentsList
  }
}
</script>

<style>
</style>



