<template>
  <span>
    <h4>I want to comment with my mail</h4>
    <table>
      <tr>
        <td>Name</td>
        <td><input v-model="name" ></td>
      </tr><tr>
        <td>Mail adress</td>
        <td><input v-model="mailadress" ></td>
      </tr>
      <tr>
        <td></td>
        <td>
          <button v-on:click="login">Leave a comment</button>
        </td>
      </tr>
    </table>
    </div>
  </span>
</template>

<script>
export default {
  props: ['loginInfo', 'onLogin'],

  methods: {
    login: function () {
      if (this.onLogin) {
        this.onLogin({
          type: 'mail',
          mail: this.$data.mailadress,
          name: this.$data.name
        })
      }
    }
  },

  data: function () {
    return {
      name: '',
      mailadress: ''
    }
  }
}
</script>



