<template>
  <g-signin-button
    :params="googleSignInParams"
    @success="onSignInSuccess"
    @error="onSignInError">
      <img src="static/google.jpg" alt="Login with Google"/>
  </g-signin-button>
</template>

<script>
import SignInButton from 'vue-google-signin-button'

export default {
  props: ['loginInfo', 'onLogin'],
  methods: {
    onSignInSuccess: function (googleUser) {
      // `googleUser` is the GoogleUser object that represents the just-signed-in user.
      // See https://developers.google.com/identity/sign-in/web/reference#users
      const profile = googleUser.getBasicProfile() // etc etc
      if (this.onLogin) {
        this.onLogin({
          type: 'google',
          mail: profile.getEmail(),
          name: profile.getName(),
          avatar: profile.getImageUrl()
        })
      }
    },

    onSignInError: function (error) {
      // `error` contains any error occurred.
      console.log('OH NOES', error)
    }
  },

  data: function () {
    return {
      name: '',
      mailadress: '',
      googleSignInParams: {
        client_id: '467955741111-2gmmibvpcd2v4f2c8a4kqr9ihusoqknu.apps.googleusercontent.com'
      }
    }
  },

  components: {
    SignInButton
  }
}
</script>



