<template>
  <div class="commentBox">
      <span class="avatar">
        <img :src="avatarUrl" />
      </span>
      <div class="name">
        {{userName}}
        <span class="date">
          <FancyDate :date="date"/>
        </span>
      </div>
      <div class="comment">
        {{text}}
      </div>
  </div>
</template>

<script>
import FancyDate from '../../components/FancyDate'

export default {

  props: ['userName', 'mail', 'avatarUrl', 'date', 'text'],
  components: {
    FancyDate
  }
}
</script>
<style>
  .commentBox {
    margin-top: 16px;
    margin-bottom: 16px;
    text-align: left;
    clear: both;
  }

  .date{
    color: #555555;
    font-weight: normal;
    font-size: 75%;
    text-decoration: italic;
    text-align: left;
  }

  .comment {
    padding-left: 64px;
    text-align: left;
  }

  .name {
    padding-left: 64px;
    color: #2196F3;
    font-weight: bold;
    padding-bottom: 4px;
    text-align: left;
  }

  .avatar img {
    width: 48px;
    height: 48px;
    float: left;
    border-radius: 4px;
  }
</style>



