<template>
  <fb-signin-button
    :params="fbSignInParams"
    @success="onSignInSuccess"
    @error="onSignInError">
    <img src="static/facebook.png" alt="Login with Facebook"/>
  </fb-signin-button>
</template>

<script>
export default {
  props: ['loginInfo', 'onLogin'],
  methods: {
    onSignInSuccess (response) {
      window.FB.api('/me?fields=picture,name', profileInfo => {
        console.log('profileInfo', profileInfo)
        if (this.onLogin) {
          this.onLogin({
            type: 'facebook',
            name: profileInfo.name,
            avatar: profileInfo.picture.data.url
          })
        }
      })
    },
    onSignInError (error) {
      console.log('OH NOES', error)
    }
  },

  data: function () {
    return {
      fbSignInParams: {
        scope: 'email,user_likes',
        return_scopes: true
      }
    }
  }
}
</script>



