<template>
  <div>
    <Heading>Post a comment</Heading>
    <div v-if="loginInfo !== undefined" class="postCommentContainer">
      <textarea
        v-model="message"
        v-on:keyup.13="postComment">
      >
      </textarea>
      <div class="postCommentToolbar">
        <button v-on:click="location.reload()">Log out</button>
        <button v-on:click="postComment">Post as {{this.loginInfo.name}} ></button>
      </div>
    </div>
    <div v-else>
      <PostCommentLogin :onLogin="onLogin" />
    </div>
  </div>
</template>

<script>
import Heading from 'theme/Heading'
import PostCommentLogin from './PostCommentLogin'

export default {
  props: ['comments', 'arrayRef', 'commentStreams'],

  methods: {
    onLogin: function (loginInfo) {
      this.loginInfo = loginInfo
    },

    postComment: function () {
      console.log('postComment', this.$props.commentStreams)

      var updateDict = {}
      updateDict[window.commentStream] = true

      this.$props.commentStreams.update(updateDict)

      this.$props.arrayRef.push({
        name: this.$data.message,
        date: new Date().toJSON(),
        moderationPending: true,
        userInfo: this.loginInfo
      })

      this.$data.message = ''
    }
  },

  components: {
    Heading,
    PostCommentLogin
  },

  data: function () {
    return {
      loginInfo: undefined,
      message: ''
    }
  }
}
</script>
<style>
</style>



